@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);
html {
    height: 100%;
}

body {
    height: 100%;
    line-height: 1;
    margin: 0;
}

*,
:after,
:before {
    box-sizing: border-box;
}

#root {
    height: 100%;
}

.page {
    height: 100%;
    font-size: 16px;
    font-family: "Inter", sans-serif;
    color: #505050;
    background-color: #f8fafb;
}

.page__search-panel {
    grid-area: search-panel;
}

.page__sidebar {
    grid-area: sidebar;
}

.page__main {
    grid-area: main;
    height: 100%;
}

.page__container {
    min-height: 100%;
    padding: 105px 30px 20px 30px;
    width: 100%;
    max-width: 1700px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 20fr 80fr;
    grid-template-rows: 88px 1fr;
    grid-gap: 30px;
    gap: 30px;
    grid-template-areas: 
    "sidebar search-panel"
    "sidebar main";
}

@media screen and (max-width: 1100px) {
    .page__container {
        padding-left: 20px;
        padding-right: 20px;
    }
}
.header {
    position: fixed;
    background-color: #fff;
    height: 75px;
    box-shadow: 0px 5px 10px -10px rgba(0, 0, 0, 0.4);
    display: flex;
    width: 100%;
    padding: 0 30px;
    justify-content: space-between;
    align-items: center;
}

.header__logo-block {
    margin-right: 10px;
}
.header__logo-link {}

.header__content {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.header__text {
    margin-right: 30px;
    font-size: 20px;
    font-weight: 500;
}

.header__text--color--highlighted {
    color: #298EFA;
}

.header__nav-bar {}

.header__nav-link {}

@media screen and (max-width: 660px) {
    .header__text {
        display: none;
    }
    
    .header__content {
        justify-content: flex-end;
    }
}
.user-profile {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
}

.user-profile__name {
    margin-right: 8px;
    display: inline-block;
    color: #666666;
}

.user-profile__avatar {
    border-radius: 50%;
    height: 32px;
    width: 32px;
    margin: 0;
}

.user-profile__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
}
.search-panel {
