    height: 14px;
}

.search-panel__select {
    margin-right: 16px;
}

.search-panel__flex-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.search-panel__input-group {
    display: flex;
    flex: 1 1;
    margin-right: 16px;
}

.search-panel__input-group input {
    border-radius: 8px;
}

.search-panel__search-button {
    background-color: transparent;
    outline: none;
    border: 1px solid #afb8bd;
    cursor: pointer;
    border-radius: 0 8px 8px 0;
    padding: 10px 15px;
    border-left: none;
    background: transparent url(/todo-app/static/media/search-icon.e00d8929.svg) no-repeat center center;
}

.search-panel__statistics {
    
}
.select-input {
    border: 1px solid #afb8bd;
    font-size: 14px;
    font-weight: 600;
    padding: 10px 15px;
    line-height: 14px;
    border-radius: 8px;
    background-color: transparent;
    outline: none;
    cursor: pointer;
}

.select-input:hover,
.select-input:focus,
.select-input:active {
    transition: 0.2s;
    background-color: #afb8bd;
}

.select-input__option {
    padding: .25rem 1.5rem;
    color: #212529;
    background-color: #fff;
}
.text-input {
    width: 100%;
    display: block;
    height: 36px;
    padding: 6px 12px;
    background-color: #fff;
    border: 1px solid #e1e1e1;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    outline: none;
    line-height: 1.5;
    border-radius: 8px;
    color: #495057;
    font-size: 0.875rem;
}

.text-input:focus,
.text-input:active {
    border-color: #007bff;
}
.todo-list {
    height: 100%;
    margin-top: 0;
    margin-bottom: 0;
    list-style: none;
    padding-left: 0;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.1);
    overflow: auto;
}

.list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 25px;
    border-bottom: 1px solid #e6e6e6;
    cursor: pointer;
    height: 67px;
}

.list-item.done {
    background-color: #ebebeb;
}

.list-item.removed {
    background-color: rgba(186,0,0,0.1);
}

.list-item.removed:hover {
    background-color: rgba(186,0,0,0.2);
    transition: 0.2s;
}

.list-item.removed .list-item__right-block-wrapper {
    padding-right: 25px;
}

.list-item.removed .list-item__delete-button {
    display: none;
}

.list-item.stared .list-item__text {
    color: #ff9f43;
}

.list-item.done .list-item__text {
    color: #28c76f;
    text-decoration: line-through;
}

.list-item:hover {
    background-color:  rgba(0, 0, 0, 0.03);
    transition: 0.2s;
}

.list-item__button-group {
    display: flex;
}

.list-item__checkbox,
.list-item__checkstar  {
    margin-right: 15px;
}

.list-item__checkbox {
    padding-top: 2px;
    margin-right: 7px;
}

.list-item__text {
    flex: 1 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 5px;
    padding-bottom: 5px;
}

.list-item__right-block-wrapper {
    display: flex;
    align-items: center;
}

.list-item__topic-tag {
    margin-right: 25px;
}

.list-item__delete-button {
    
