    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
}

.star-button.active  .star-button__icon g {
    fill: #e9882e;
} 

.star-button:focus .star-button__icon g{
    fill: #e9882e;
}

.star-button__icon {
    width: 16px;
    height: 16px;
}
.topic-tag {
    font-weight: 500;
    font-size: 0.75rem;
    padding: 5px 10px;
    color: #fff;
    border-radius: 4px;
}
.background--green {
    background-color: #28c76f;
}

.background--red {
    background-color: #ea5455;
}

.background--blue {
    background-color: #33b5e5;
}

.background--orange {
    background-color: #ff9f43;
}

.background--purple {
    background-color: #AA68CC;
}

.background--dark-green {
    background-color: #1f851c;
}

.background--default {
    background-color: rgb(211, 209, 80);
}
.delete-button {
    transition: color 0.2s;
    color: #666666;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
}

.delete-button:hover,
.delete-button:focus,
.delete-button:active {
    color: red;
}

.delete-button__icon {
    width: 16px;
    height: 16px;
}
.sidebar {
    background-color: #fff;
    box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.1);
    height: 100%;
    border-radius: 8px;
}

.sidebar__button-wrapper {
    padding: 24px;
    margin-bottom: 20px;
}

.sidebar__button {
    width: 100%;
    outline: none;
    background-color: #aa66cc;
    cursor: pointer;
    font-size: 14px;
    border-radius: 8px;
    line-height: 14px;
    color: #fff;
    border: none;
    padding: 10px 15px;
    transition: 0.15s;
    height: 36px;
}

.sidebar__button:hover,
.sidebar__button:focus,
.sidebar__button:active {
    background-color: #9159ac;
}

.modal-window {
    position: relative;
    background-color: rgba(0, 0, 0, 0);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 65px 0;
    overflow-y: auto;
    max-height: 100vh;
    font-family: Helvetica;
}

.modal-window.open {
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 50;
}

.modal-window.open .modal-window__window {
    display: block;
}

.modal-window__window {
    min-width: 500px;
    display: none;
    position: relative;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: 1px solid #dcdcdc;
    border-radius: 6px;
    background-color: #fbfbfb;
}

.modal-window__container {
    padding: 30.5px;
    border: 1px solid transparent;
    border-radius: 6px;
}

.modal-window__header {
    padding: 25px 20px;
    background-color: #ebebeb;
    border-radius: 6px 6px 0 0; 
}


.modal-window__header-title {
    font-size: 17px;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 0;
    border-radius: 6px;
}

.modal-window__close-button {
    width: 18px;
    height: 18px;
    position: absolute;
    right: -30px;
    top: -1px;
    cursor: pointer;
    border: none;
    outline: none;
    background: transparent url(/todo-app/static/media/close-icon.76b09ab8.svg) no-repeat center center;
    background-size: cover;
}

.modal-window__close-button:focus {
    transition: .2s;
    box-shadow: 0px 0px 11px 0px rgba(255,255,255,0.88);
}

.modal-window__back-button-wrapper {
    border: 1px solid transparent;
    border-radius: 6px;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-bottom: 1px solid #dcdcdc;
}

.modal-window__back-button {
    text-decoration: none;
    font-size: 1.125rem;
    line-height: 1.375rem;
    color: #c8c8c8;
    padding-left: 20px;
    padding: 14px 20px;
    border: none;
    background-color: transparent;
    outline: none;
}

@media screen and (max-width: 667px) {
    .modal-window {
        padding: 0;
    }
    
    .modal-window__window {
        border-radius: 0;
        width: 100%;
        border: none;
        min-height: 100%;
    }
    
    .modal-window__container {
        padding: 20px;
    }
    
    .modal-window__button {
        width: 100%;
    }
    
    .modal-window__close-button {
        display: none;
    }
    
    .modal-window__back-button-wrapper {
        display: flex;
    }
}
