.tag-list {
    list-style: none;
    margin: 0;
    padding-left: 0;
}

.tag-list__item {
    padding: 12px 24px;
    cursor: pointer;
    border-bottom: 1px solid #e6e6e6;;
}

.tag-list__item:first-child .tag-list__icon {
    display: none;
}

.tag-list__item:first-child .tag-list__text {
