}

.tag-list__item:first-child .tag-list__text:before {
    content: '';
    top: 2px;
    left: 0;
    width: 17px;
    height: 17px;
    position: absolute;
    background: url(/todo-app/static/media/all-icon.77405aed.svg) no-repeat center center;
    background-size: cover;
}

.tag-list__item:last-child {
